// Function to extract and normalize data from Excel documentation
export const extractExcelBodyContent = (apiDocumentation) => {
  const bodyContent = {};
  const lines = apiDocumentation.split("\n");
  let currentKey = "";

  lines.forEach((line) => {
    if (line.startsWith("  Content-Type:")) {
      currentKey = line.split(": ")[1].trim();
      bodyContent[currentKey] = {};
    } else if (currentKey && line.trim().startsWith("type:")) {
      bodyContent[currentKey]["type"] = line.split(": ")[1].trim();
    } else if (currentKey && line.trim().startsWith("mandatoryness:")) {
      bodyContent[currentKey]["mandatoryness"] = line.split(": ")[1].trim();
    }
  });
  return bodyContent;
};

// Function to compare the two sets of body content
export const compareData = (excelContent, jsonContent) => {
  const result = [];
  for (const key in excelContent) {
    if (jsonContent[key]) {
      if (
        excelContent[key].type !== jsonContent[key].type ||
        excelContent[key].mandatoryness !== jsonContent[key].mandatoryness
      ) {
        result.push({ key, excel: excelContent[key], json: jsonContent[key] });
      }
    } else {
      result.push({ key, excel: excelContent[key], json: "Not found in JSON" });
    }
  }
  for (const key in jsonContent) {
    if (!excelContent[key]) {
      result.push({ key, excel: "Not found in Excel", json: jsonContent[key] });
    }
  }
  return result;
};

