import React, { useState } from "react";
import { Modal, Button } from "react-bootstrap";
import { readExcelFile } from "./readExcelFile";
import { readJsonFile } from "./readJsonFile";
import { extractExcelBodyContent } from "./extractBodyParts";
import { compareData } from "./extractBodyParts";
import ComparisonComponent from "./ComparisonComponent";

const HeadBodyModal = ({ show, handleClose, endpoint }) => {
  const [excelFile, setExcelFile] = useState(null);
  const [jsonFile, setJsonFile] = useState(null);
  const [comparisonResults, setComparisonResults] = useState([]);
  const [modalVisible, setModalVisible] = useState(false);

  const handleExcelFileChange = (event) => {
    setExcelFile(event.target.files[0]);
  };

  const handleJsonFileChange = (event) => {
    setJsonFile(event.target.files[0]);
  };

  const handleCompareFiles = async () => {
    if (!excelFile || !jsonFile) {
      alert("Please upload both Excel and JSON files.");
      return;
    }

    try {
      const [excelData, jsonData] = await Promise.all([
        readExcelFile(excelFile),
        readJsonFile(jsonFile),
      ]);

      const excelBodyContent = extractExcelBodyContent(
        excelData.find((row) => row[3] === endpoint.url)
      );
      const jsonBodyContent = {};
      jsonData.item[0].item[0].request.body.urlencoded.forEach((item) => {
        if (item.url === endpoint.url) {
          jsonBodyContent[item.key] = {
            type: item.value,
            mandatoryness: item.description.includes("(Required)"),
          };
        }
      });

      const comparisonResult = compareData(excelBodyContent, jsonBodyContent);
      setComparisonResults(comparisonResult);
      setModalVisible(true);
    } catch (error) {
      console.error("Error comparing files:", error);
    }
  };

  return (
    <>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Endpoint Details</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <h5>URL:</h5>
          <p>{endpoint?.url}</p>
          <h5>Head/Body Comparison:</h5>
          {/* Display comparison results */}
          {endpoint && endpoint.comparisonResult && (
            <div>
              <h5>Comparison Results:</h5>
              <ul>
                {endpoint.comparisonResult.map((result, index) => (
                  <li key={index}>
                    <strong>{result.key}</strong>
                    <ul>
                      <li>Excel: {JSON.stringify(result.excel)}</li>
                      <li>JSON: {JSON.stringify(result.json)}</li>
                    </ul>
                  </li>
                ))}
              </ul>
            </div>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
      {/* Render the ComparisonComponent */}
      <ComparisonComponent
        excelFile={excelFile}
        jsonFile={jsonFile}
        selectedEndpoint={endpoint}
        setModalVisible={setModalVisible}
        modalVisible={modalVisible}
      />
    </>
  );
};

export default HeadBodyModal;
