import React, { useState, useEffect } from "react";
import { readExcelFile } from "./readExcelFile.js";
import { readJsonFile } from "./readJsonFile.js";
import { extractExcelBodyContent, compareData } from "./extractBodyParts";
import HeadBodyModal from "./headBodyModal";

const ComparisonComponent = ({
  excelFile,
  jsonFile,
  selectedEndpoint,
  setModalVisible,
  modalVisible,
}) => {
  const [comparisonResults, setComparisonResults] = useState([]);

  useEffect(() => {
    const compareFiles = async () => {
      try {
        // Read Excel file
        const excelData = await readExcelFile(excelFile);
        const apiDocumentation = excelData[1][5]; // Adjust index to match api_documentation column

        // Read JSON file
        const jsonData = await readJsonFile(jsonFile);

        // Extract body content from Excel data
        const excelBodyContent = extractExcelBodyContent(apiDocumentation);

        // Extract body content from JSON data
        const jsonBodyContent = {};
        jsonData.item[0].item[0].request.body.urlencoded.forEach((item) => {
          jsonBodyContent[item.key] = {
            type: item.value,
            mandatoryness: item.description.includes("(Required)"),
          };
        });

        // Compare the two sets of body content
        const comparisonResult = compareData(excelBodyContent, jsonBodyContent);

        // Set the comparison result in state
        setComparisonResults(comparisonResult);
        setModalVisible(true);
      } catch (error) {
        console.error("Error:", error);
      }
    };

    if (selectedEndpoint && excelFile && jsonFile) {
      compareFiles();
    }
  }, [excelFile, jsonFile, selectedEndpoint, setModalVisible]);

  return (
    <HeadBodyModal
      show={modalVisible}
      handleClose={() => setModalVisible(false)}
      endpoint={selectedEndpoint}
      comparisonResult={comparisonResults}
    />
  );
};

export default ComparisonComponent;
